import * as React from 'react';
import { graphql } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Layout from '../../components/layout';
import Seo from '../../components/seo';
import * as style from '../../styles/style.module.css';

const MemoPost = ({ data }) => {
  const image = getImage(data.mdx.frontmatter.hero_image);
  return (
    <Layout pageTitle={data.mdx.frontmatter.title}>
      <Seo
        pageTitle={data.mdx.frontmatter.title}
        pageDescription={data.mdx.frontmatter.description}
        imgPath={`/${data.mdx.slug}${data.mdx.frontmatter.hero_image.base}`}
      />
      <p className={style.postDated}>
        投稿日：
        <time datetime={data.mdx.frontmatter.date}>
          {data.mdx.frontmatter.date.slice(0, 10).replace(/-/g, ' / ')}
        </time>
      </p>
      <GatsbyImage
        image={image}
        alt={data.mdx.frontmatter.hero_image_alt}
      />
      <MDXRenderer>
        {data.mdx.body}
      </MDXRenderer>
    </Layout>
  )
};

export const query = graphql`
  query ($id: String) {
    mdx(id: {eq: $id}) {
      body
      slug
      frontmatter {
        title
        description
        date(formatString: "YYYY-MM-DD HH:MM")
        hero_image {
          childImageSharp {
            gatsbyImageData
          }
          base
        }
        hero_image_alt
      }
    }
  }
`;

export default MemoPost;